import React from 'react';
import { useLoaderData, Link } from "react-router-dom";
import axios from '../utility/axios';
import { Heading, HStack, Button, Card, Text } from 'rsuite';

import Product from '../components/Product';

export async function loader({ params }) {
	let data = JSON.stringify({
		"lang": "uz",
		"orgUnitId": "1000034185670",
	 });

	const config = {
		method: 'post',
		url: 'https://mobile-api.pr2.flnet.su/gate/v3/homepageV2?orgunit=1000034185670&storeId=40761937696&lang=uz',
		headers: { 
			'Content-Type': 'application/json'
		 },
		data : data
	};
	
	const response = await axios(config);
	const homeProducts = response.data.items;

   console.log(response.data);

	return { homeProducts };
}

const Section = ({ title, items }) => (
   <div className="section mb-5" key={title}>
      <HStack className='mb-5' spacing={5} justifyContent='space-between' alignItems='center'>
         <Heading level={5}>{title}</Heading>
         {/* <Button appearance="link" color="red" endIcon={<ArrowRightLineIcon />} >Barchasini ko'rish</Button> */}
      </HStack>
      <div className='product-list'>
         {items.map((product, index) => (
            <Product product={product} key={index} />
         ))}
      </div>
   </div>
);

const Page = () => {
   const { homeProducts  } = useLoaderData();

   return (
      <>
         <Section title="Yangi tovarlar" items={homeProducts[0].components[0].products} />
         <Section title="Eng ko'p sotilgan" items={homeProducts[1].components[0].products} />
         <Section title="Aksiyalar" items={homeProducts[2].components[0].products} />
      </>
   );
};

export default Page;
