import React from 'react';
import { Link, Outlet, useNavigate } from "react-router-dom";
import axios from './utility/axios';
import { Container, Header, Navbar, VStack, HStack, Content, Avatar, Button, Text, Drawer, Sidenav, Nav, Modal, Divider, Badge } from 'rsuite';
import 'rsuite/dist/rsuite.min.css';
import MenuIcon from '@rsuite/icons/Menu';
import GridIcon from '@rsuite/icons/Grid';
import ArrowLeftLineIcon from '@rsuite/icons/ArrowLeftLine';
import ScrollToTop from "./components/ScrollToTop";
import store from "./store";
import { useHookstate } from '@hookstate/core';

import './App.css'
import 'bootstrap-icons/font/bootstrap-icons.css';

function App() {
	const state = useHookstate(store);
	const [parentCategoryName, setParentCategoryName] = React.useState('');
	const [categories, setCategories] = React.useState([]);
	const [subcategories, setSubcategories] = React.useState([]);
	const [menu, setMenu] = React.useState(false);
	const [categMenu, setCategMenu] = React.useState(false);
   const [subcategMenu, setSubcategMenu] = React.useState(false);

	const navigate = useNavigate();

	function getSubcategories(categ) {

		const config = {
			method: 'post',
			url: 'https://mobile-api.pr2.flnet.su/gate/v3/categoriesTreeV2?orgunit=1000034185670&categoryId='+ categ.partnerId +'&lang=uz',
			headers: { }
		};

		setParentCategoryName(categ.name);
		
		axios(config)
		.then((response) => {
			let data = response.data;
			console.log(data)

			setSubcategories(data.values)
			setSubcategMenu(true)
		})
		.catch(function (error) {
			console.log(error);
		});
 
	}

	React.useEffect(() => {
		const config = {
			method: 'post',
			url: 'https://mobile-api.pr2.flnet.su/gate/v3/categoriesV2?orgunit=1000034185670&lang=uz',
			headers: { }
		};
		
		const itemsToRemove = ["NewCategoryLink", "HitCategoryLink", "Chegirmali savdo", "SaleCategoryLink", "p1-For-business", "HistoryCategoryLink", "presents", "SaleByTagCategoryLink"];

		axios(config)
		.then((response) => {
			let data = response.data;
			console.log(data)

			let _categories = data.categories.filter((item) => !itemsToRemove.includes(item.uid))

			_categories.forEach((el, index) => {
				if(el.name == "Xizmat") _categories[index].name = 'Parvarishlash';
			});
			
			setCategories(_categories)
		})
		.catch(function (error) {
			console.log(error);
		});
		 
	}, [])

	return (
		<div className="App">
			<Container>
				<Header className='mb-5'>
					<Navbar className='py-2' appearance='subtle'>
						<div className='max-w-screen-xl mx-auto px-3'>
							<div className='mb-2'>
								<Text size={'xxl'} weight="bold" align='center'><Link>Faberlic</Link></Text>
								<Text weight="medium" size={'md'} align='center' color='red'><Link>Umida Ochilova</Link></Text>
							</div>
							<HStack className='desktop-nav' spacing={5} justifyContent='space-between' alignItems='center'>
								<Button 
									onClick={() => setCategMenu(true)} 
									startIcon={<GridIcon />} 
								>
									Katolog
								</Button>
								<Nav>
									<Nav.Item as={Link} to='/'>Bosh sahifa</Nav.Item>
									<Nav.Item as={Link} to='/advice'>Bepul maslahat</Nav.Item>
									<Nav.Item as={Link} to='/how-to-order'>Qanday buyurtma qilish</Nav.Item>
									<Nav.Item as={Link} to='/contact'>Bog'lanish</Nav.Item>
									<Nav.Item as={Link} to='/about'>Sayt haqida</Nav.Item>
								</Nav>
									<Button 
										onClick={() => navigate('order')} 
									>
										<Text muted>
											{ state.cartItems.value.length ? (
												<Badge as={'span'} color="red" content={ state.cartItems.value.length } />
											) : (
												<>0</>
											)}
										</Text>
										<i className="bi bi-cart ml-1 mr-2 text-lg" />
										Savat
									</Button>
							</HStack>
							<HStack className='mobile-nav' spacing={5} justifyContent='space-between' alignItems='flex-end'>
								<Button 
									onClick={() => setCategMenu(true)} 
									startIcon={<GridIcon />} 
									size='sm'
								>
									Katolog
								</Button>
								<Button 
									onClick={() => navigate('order')} 
									size='sm'
								>
									<Text muted>
										{ state.cartItems.value.length ? (
											<Badge as={'span'} color="red" content={ state.cartItems.value.length } />
										) : (
											<>0</>
										)}
									</Text>
									<i className="bi bi-cart ml-1 mr-2" />
									Savat
								</Button>
								<Button 
									onClick={() => setMenu(true)} 
									startIcon={<MenuIcon />} 
									size='sm'
								>
									Menu
								</Button>
							</HStack>
						</div>
					</Navbar>
					<Divider className='mt-1' />
				</Header>
				<Drawer size={270} open={menu} onClose={() => setMenu(false)}>
					<Drawer.Header>
						<Drawer.Title>Menu</Drawer.Title>
					</Drawer.Header>
					<Sidenav appearance='subtle'>
						<Sidenav.Body>
							<Nav>
								<Nav.Item as={Link} onClick={() => setMenu(false)} to='/'>Bosh sahifa</Nav.Item>
								<Nav.Item as={Link} onClick={() => setMenu(false)} to='/advice'>Bepul maslahat</Nav.Item>
								<Nav.Item as={Link} onClick={() => setMenu(false)} to='/how-to-order'>Qanday buyurtma qilish</Nav.Item>
								<Nav.Item as={Link} onClick={() => setMenu(false)} to='/contact'>Bog'lanish</Nav.Item>
								<Nav.Item as={Link} onClick={() => setMenu(false)} to='/about'>Sayt haqida</Nav.Item>
							</Nav>
						</Sidenav.Body>
					</Sidenav>
				</Drawer>
				<Modal open={categMenu} size='full'>
					<Modal.Header closeButton={false}>
						<Button className='!pl-0' appearance="subtle" startIcon={<ArrowLeftLineIcon />}  onClick={() => setCategMenu(false)}>Orqaga</Button>
					</Modal.Header>
					<Modal.Body>
						<HStack wrap className='category-list' alignItems='flex-start'>
							{categories.map((categ, index) => (
								<div key={index} className='category' onClick={() => getSubcategories(categ)}>
									<img key={index} src={categ.image}/>
									<Text className='pt-2' align='center'>{categ.name}</Text>
								</div>
							))}
						</HStack>
					</Modal.Body>
				</Modal>
				<Modal className='subcategory-menu' open={subcategMenu} size='full'>
					<Modal.Header closeButton={false}>
						<Button className='!pl-0' appearance="subtle" startIcon={<ArrowLeftLineIcon />} onClick={() => setSubcategMenu(false)}>Orqaga</Button>
					</Modal.Header>
					<Modal.Body>
						<Sidenav appearance='subtle'>
							<Sidenav.Body>
								<Nav>
									{subcategories.map((categ, index) => (
										categ.values.length ? (
											<Nav.Menu key={index} title={categ.name}>
												{categ.values && categ.values.map((subcateg, index) => (
													<Nav.Item 
														as={Link} 
														to={'listing/' + subcateg.code } 
														state={{ 
															pageTitle: subcateg.name, 
															breadcrumb: [
																parentCategoryName,
																categ.name,
																subcateg.name
															] 
														}}
														key={index} 
														onClick={() => { setCategMenu(false); setSubcategMenu(false) }}
													>
														{ subcateg.name }
													</Nav.Item>
												))}
											</Nav.Menu>
										) : (
											<Nav.Item 
												as={Link} 
												to={'listing/' + categ.code } 
												state={{ 
													pageTitle: categ.name, 
													breadcrumb: [
														parentCategoryName,
														categ.name,
														categ.name
													] 
												}}
												key={index} 
												onClick={() => { setCategMenu(false); setSubcategMenu(false) }}
											>
												{ categ.name }
											</Nav.Item>
										)
									))}
								</Nav>
							</Sidenav.Body>
						</Sidenav>
					</Modal.Body>
				</Modal>
				<Content className='max-w-screen-xl w-full mx-auto px-3 pb-10'>
					<Outlet />
				</Content>
			</Container>
			<div className='preloaded-images'>
				{categories.map((categ, index) => (
					<img key={index} src={categ.image}/>
				))}
			</div>
			<ScrollToTop/>
		</div>
	);
}

export default App;
