import axs from 'axios';


const axios = axs.create();

axios.interceptors.request.use((config) => {
  const prependText = 'https://pozitivchi.uz/proxy?url='; 
  // const prependText = 'http://localhost:3001/proxy?url='; 
  // const prependText = ''; 
  config.url = `${prependText}${config.url}`; 
  return config;
}, (error) => Promise.reject(error));

export default axios;

