import { useState, useEffect } from 'react';
import { useLoaderData, useLocation } from "react-router-dom";
import axios from '../utility/axios';
import { Heading, Breadcrumb, Pagination, Text, Button } from 'rsuite';
import Product from '../components/Product';

export async function loader({ params }) {
   const catagoryId = params.catagoryId;

	let data = JSON.stringify({
		"lang": "uz",
		"orgUnitId": "1000034185670",
		"categoryId": catagoryId,
		"type": "CATEGORIES",
		"offset": 0
	 });

	const config = {
		method: 'post',
		url: 'https://mobile-api.pr2.flnet.su/gate/v3/listProducts?lang=uz',
		headers: { 
			'Content-Type': 'application/json'
		 },
		data : data
	};
	
	const response = await axios(config);
	const _products = response.data.products;
   const _hasNextPage = response.data.hasNextPage;
   console.log(response.data);

	return { _products, _hasNextPage, catagoryId };
}

const Page = () => {
   const { _products, _hasNextPage, catagoryId } = useLoaderData();
   const [products, setProducts] = useState(_products);
   const [hasNextPage, setHasNextPage] = useState(_hasNextPage);
   const [offset, setOffset] = useState(0);
   const [loading, setLoading] = useState(false);
   const location = useLocation();

   async function loadMore() {
      setLoading(true);
      const _offset = offset + 9;
      setOffset(_offset);

      
      try {
         let data = JSON.stringify({
            "lang": "uz",
            "orgUnitId": "1000034185670",
            "categoryId": catagoryId,
            "type": "CATEGORIES",
            "offset": _offset
          });
      
         const config = {
            method: 'post',
            url: 'https://mobile-api.pr2.flnet.su/gate/v3/listProducts?lang=uz',
            headers: { 
               'Content-Type': 'application/json'
             },
            data : data
         };

         const response = await axios(config);
         setProducts((val) => {
            return [...val, ...response.data.products];
         });
         setHasNextPage(response.data.hasNextPage);
   
         console.log(response.data);
         
      } catch (error) {
         console.log(error)
      }

      setLoading(false);
   }

   useEffect(() => {
      setProducts(_products);
      setHasNextPage(_hasNextPage);
      setOffset(0); // Reset offset on category change

      console.log('_hasNextPage')
      console.log(_hasNextPage)
   }, [_products, _hasNextPage, catagoryId, location.key]);

   return (
      <>
         <Heading>{ location.state.pageTitle }</Heading>
         <Breadcrumb>
            {location.state.breadcrumb.map((item, index) => (
               <Breadcrumb.Item 
                  key={index} 
                  href="/components/overview"
                  as={Text}
               >
                  { item }
               </Breadcrumb.Item>
            ))}
         </Breadcrumb>
         <div className='product-list'>
            {products.map((product, index) => (
               <Product product={product} key={index} />
            ))}
         </div>
         { hasNextPage && (
            <Button 
               size='lg' 
               className='max-w-[300px] mx-auto block w-full'
               onClick={() => loadMore()}
               loading={loading}
            >
               Yana ko'rsatish
            </Button>
         )}
      </>
   );
};

export default Page;

