import React from 'react';
// import * as ReactDOM from "react-dom/client";
import ReactDOM from 'react-dom'; // Use ReactDOM from 'react-dom' for React 17
import {
  createBrowserRouter,
  RouterProvider,
} from "react-router-dom";
import './index.css';
import App from './App';
import * as serviceWorkerRegistration from './serviceWorkerRegistration';
import reportWebVitals from './reportWebVitals';

import HomePage, { loader as homeProductsLoader } from './pages/Home'
import ListingPage, { loader as productsLoader } from './pages/Listing'
import ProductPage, { loader as productLoader } from './pages/Product'
import OrderPage from './pages/Order'
import AdvicePage from './pages/nav/Advice'
import HowToOrderPage from './pages/nav/HowToOrder'
import ContactPage from './pages/nav/Contact'
import AboutPage from './pages/nav/About'

const router = createBrowserRouter([
  {
    path: "/",
    element: <App />,
    children: [
      {
        index: true,
        element: <HomePage />,
        loader: homeProductsLoader,
      },
      {
        path: '/listing/:catagoryId',
        element: <ListingPage />,
        loader: productsLoader,
      },
      {
        path: '/product/:productId',
        element: <ProductPage />,
        loader: productLoader,
      },
      {
        path: '/order',
        element: <OrderPage />,
      },
      {
        path: '/advice',
        element: <AdvicePage />,
      },
      {
        path: '/how-to-order',
        element: <HowToOrderPage />,
      },
      {
        path: '/contact',
        element: <ContactPage />,
      },
      {
        path: '/about',
        element: <AboutPage />,
      }
    ]
  },
]);

ReactDOM.render(
  <React.StrictMode>
    <RouterProvider router={router} />
  </React.StrictMode>,
  document.getElementById('root') // Use ReactDOM.render instead of createRoot
);

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://cra.link/PWA
serviceWorkerRegistration.unregister();

// If you want to start measuring performance in your app, pass a function
// to log results (for example: reportWebVitals(console.log))
// or send to an analytics endpoint. Learn more: https://bit.ly/CRA-vitals
reportWebVitals();
