import { useState, useMemo } from 'react';
import { Text, Table,  Stat, useToaster, Button, Form, Schema, Panel, FlexboxGrid, Col, Message } from 'rsuite';
import store from "../store";
import { useHookstate } from '@hookstate/core';
import axios from 'axios';

const { Column, HeaderCell, Cell } = Table;
const { StringType } = Schema.Types;


const infoMessage = (
   <Message showIcon type='info' closable>Buyurtmangiz qabul qilindi. Buyurtmangizni tasdiqlash uchun tez orada siz bilan bog'lanamiz.</Message>
);

const errorMessage = (
   <Message showIcon type='error' closable>Xatolik yuz berdi. Qaytadan urinib ko'ring!</Message>
);

const Page = () => {
	const toaster = useToaster();
	const state = useHookstate(store);
	const cartItems = useMemo(() => {
		let items = [];
		
		if(state.cartItems.value) {
			state.cartItems.value.forEach((item) => {
				items.push({ 
					id: item.code,
					imageUrl: item.images[0].url,
					name: item.name,
					price: item.catalogPrice.value.toLocaleString('ru-RU') + " so'm",
				})
			})
		}

		return items;

	}, [state.cartItems])

	const totalPrice = useMemo(() => {
		let total = 0;

		state.cartItems.value.forEach((item) => {
			total = total + item.catalogPrice.value
		})

		return total;

	}, [state.cartItems])
	
	function removeFromCart(productId) {
		const index = state.cartItems.value.findIndex(item => item.code === productId);

		if (index === -1) return;
		
		state.cartItems.set(items => {
			let _items = [...items];
			_items.splice(index, 1);

			return _items;
		});
	}

	const [ form, setForm ] = useState({ name: '', phone: '+998' });
	const [ loading, setLoading ] = useState(false);

	async function handleSubmit() {
		setLoading(true)
		let products = [];

		state.cartItems.value.forEach((item) => {
			products.push({ 
				id: item.code,
				art: item.art,
				url: item.url,
				name: item.name,
				price: item.catalogPrice.value.toLocaleString('ru-RU') + " so'm",
			})
		})

		let data = JSON.stringify({
			products: products,
			totalPrice: totalPrice.toLocaleString('ru-RU'),
			receiverChatId: 506695006,
			buyerName: form.name,
			buyerPhone: form.phone
		 });

		try {
			const config = {
				method: 'post',
				url: 'https://pozitivchi.uz/send_order',
				headers: { 
					'Content-Type': 'application/json'
				 },
				data : data
			};
			
			const response = await axios(config);
			toaster.push(infoMessage, { duration: 5000})
			state.cartItems.set([])
		} catch (error) {
			console.log(error)
			toaster.push(errorMessage, { duration: 5000})
		}
		
		setLoading(false)
	}

	const ImageCell = ({ rowData, dataKey, ...props }) => (
		<Cell {...props} style={{ padding: 0 }}>
			<img className='h-full w-auto' src={rowData.imageUrl} />
		</Cell>
	);
	
	const ActionCell = ({ rowData, dataKey, ...props }) => (
		<Cell {...props} style={{ padding: 0 }}>
			<Button appearance='link' color='red' onClick={() => removeFromCart(rowData.id)}>
				<i className='bi bi-trash text-xl'/>
			</Button>
		</Cell>
	);

	const model = Schema.Model({
		name: StringType().isRequired('Ismingizni kiritishingiz shart'),
		phone: StringType().isRequired('Telefon raqamingizni kiritishingiz shart.').minLength(13, 'Noto\'g\'ri telefon raqam kiritildi.').maxLength(13, 'Noto\'g\'ri telefon raqam kiritildi.')
	});
	
	return (
		<>
			<Text className='mb-10' align='center' size='xxl'>Buyurtma qilish</Text>

			{cartItems.length ? (
				<Panel bordered>
					<Table
						className='custom-table mb-10'
						// height={400}
						autoHeight
						minHeight={400}
						data={cartItems}
						onRowClick={rowData => {
							console.log(rowData);
						}}
						rowHeight={(rowData) => {
							return 110;
						}}
					>
						<Column  align="center" flexGrow={0}>
							<HeaderCell>Rasmi</HeaderCell>
							<ImageCell dataKey="imageUrl" />
						</Column>

						<Column flexGrow={1} minWidth={100}>
							<HeaderCell>Nomi</HeaderCell>
							<Cell dataKey="name" />
						</Column>

						<Column flexGrow={1}>
							<HeaderCell>Narxi</HeaderCell>
							<Cell dataKey="price" />
						</Column>
						<Column  align="center" width={70}>
							<HeaderCell>. . .</HeaderCell>
							<ActionCell dataKey="id" />
						</Column>
					</Table>
					<Form fluid model={model} onSubmit={() => handleSubmit()}>
						<FlexboxGrid justify='end'>
							<FlexboxGrid.Item as={Col} colspan={24} md={6}>
								<Form.Group controlId="name" className='mr-5 mb-3'>
									<Form.ControlLabel>Ismingiz</Form.ControlLabel>
									<Form.Control name="name" value={form.name} onChange={(val) => setForm({name: val, phone: form.phone})} />
								</Form.Group>
							</FlexboxGrid.Item>
							<FlexboxGrid.Item as={Col} colspan={24} md={6}>
								<Form.Group controlId="phone" className='mr-5 mb-3'>
									<Form.ControlLabel>Telefon raqamingiz</Form.ControlLabel>
									<Form.Control 
										autoComplete='false'
										name="phone" 
										type="phone"  
										pattern="[0-9+]*"
										inputMode='numeric'
										value={form.phone} 
										onChange={(val, e) => {
											let value = e.target.validity.valid ? val : form.phone;

											setForm({name: form.name, phone: value})
										}} 
									/>
								</Form.Group>
							</FlexboxGrid.Item>
							<FlexboxGrid.Item as={Col} colspan={24} lg={4} md={6}>
								<Stat className='mr-5 mb-3'>
									<Stat.Label>Jami so'mma</Stat.Label>
									<Stat.Value>{totalPrice.toLocaleString('ru-RU')} so'm</Stat.Value>
								</Stat>
							</FlexboxGrid.Item>
							<FlexboxGrid.Item as={Col} colspan={24} md={4}>
								<Button className='mt-5' size='lg' appearance='primary' loading={loading} type='submit'>Buyurtma qilish</Button>
							</FlexboxGrid.Item>
						</FlexboxGrid>
					</Form>
				</Panel>
			) : (
				<Text align='center' size='xl' muted>Savatingizda hali hech qanday mahsulot yo'q.</Text>
			)}
			
		</>
	);
};

export default Page;

