import { useEffect, useState } from 'react';
import { useLoaderData, Link } from "react-router-dom";
import axios from '../utility/axios';
import { Carousel,  FlexboxGrid, Col, Tabs, Button, Message, Text, HStack, Divider, useToaster } from 'rsuite';
import Product from '../components/Product';
import store from "../store";
import { useHookstate } from '@hookstate/core';


const infoMessage = (
   <Message showIcon type='info' closable>Mahsulot buyurtma qilish uchun savatga qo'shildi.</Message>
);

const errorMessage = (
   <Message showIcon type='error' closable>Mahsulot savatdan olib tashlandi.</Message>
);

export async function loader({ params }) {
	const config = {
		method: 'post',
		url: 'https://mobile-api.pr2.flnet.su/gate/v3/productDetails?productId='+ params.productId +'&orgunit=1000034185670&lang=uz',
		headers: { 
			'Content-Type': 'application/json'
		 },
	};
	
	const response = await axios(config);
	const combined = response.data.combined;
	const product = response.data.product;

   console.log(response.data);

	return { product, combined };
}

const Page = () => {
	const state = useHookstate(store);
	const toaster = useToaster();
	const [addedToCart, setAddedToCart] = useState(false);
   const { product, combined  } = useLoaderData();
	const [activeCarouselIndex, setActiveCarouselIndex] = useState(0)

	const carouselItemsLength = product.images.filter((image) => image.format == 'product').length;

	function prev() {
		setActiveCarouselIndex((current) => {
			if( current == 0 ) return carouselItemsLength - 1;

			return current - 1;
		});
	}

	function next() {
		setActiveCarouselIndex((current) => {
			if( (current + 1) == carouselItemsLength) return 0;

			return current + 1;
		});
	}

	function addToCart(product) {
      const index = state.cartItems.value.findIndex(item => item.code === product.code);
      
      if (index === -1) {
         state.cartItems.set(items => [...items, product]);

			toaster.push(infoMessage, { duration: 7000})
      } else {
         state.cartItems.set(items => {
            let _items = [...items];
            _items.splice(index, 1);

            return _items;
         });

			toaster.push(errorMessage, { duration: 7000})
      }

      console.log(state.cartItems.value)
   }

	useEffect(() => {
		let added = false;

		state.cartItems.value.forEach((item) => {
			if(item.code == product.code) added = true;
		})

		setAddedToCart(added)
	}, [state.cartItems])

   return (
      <>
			<FlexboxGrid>
				<FlexboxGrid.Item as={Col} colspan={24} md={12}>
					<div className='carousel-container'>
						<Carousel 
							activeIndex={activeCarouselIndex} 
							className="custom-slider"
							onSelect={(index) => setActiveCarouselIndex(index)}
						>
							{product.images.map((image, index) => image.format != 'product' ? null : (
								<div className='carousel-item' key={index}>
									<img src={image.url} />
								</div>
							))}
						</Carousel>
						<Button 
							appearance='primary'
							className='carousel-controller left' 
							onClick={() => prev()}
						> 
							<i className='bi bi-arrow-left'/>
						</Button>
						<Button 
							appearance='primary'
							className='carousel-controller right' 
							onClick={() => next()}
						> 
							<i className='bi bi-arrow-right'/>
						</Button>
					</div>
				</FlexboxGrid.Item>
				<FlexboxGrid.Item as={Col} colspan={24} md={12}>
					<Text className='mb-2 mobile-hidden' size={30} >{product.name}</Text>
					<Text className='mb-1 desktop-hidden' size={'xxl'} >{product.name}</Text>
					<HStack className='mb-10'>
						<Text weight='bold'>Artikul:</Text>
						<Text>{product.art}</Text>
					</HStack>

					<Text weight='bold'>Narxi:</Text>
					<Text color='red' size='xxl'>{product.catalogPrice.value.toLocaleString('ru-RU')} so'm</Text>
					<Text className='line-through-text mb-3' muted size='lg'>{product.blackPrice.value.toLocaleString('ru-RU')} so'm</Text>

					<Button 
						className='mb-10 !px-10'
						onClick={() => addToCart(product)} 
						appearance={addedToCart ? 'ghost': 'primary'} 
						color='orange' 
						size="sm"
					>
						{addedToCart ? (
							<> <i className='bi bi-cart-check mr-2 text-xl' /> Qo'shilgan </>
							) : (
							<> <i className='bi bi-cart-plus mr-2 text-xl' /> Savatga </>
						)}
					</Button>

					<Tabs defaultActiveKey="1" className='mb-10'>
						<Tabs.Tab eventKey="1" title="Mahsulot haqida">
							<div dangerouslySetInnerHTML={{ __html: product.description }} />
						</Tabs.Tab>
						<Tabs.Tab eventKey="2" title="Tarkibi">
							<div dangerouslySetInnerHTML={{ __html: product.ingredients }} />
						</Tabs.Tab>
						<Tabs.Tab eventKey="3" title="Qo'llanishi">
							<div dangerouslySetInnerHTML={{ __html: product.howToApply }} />
						</Tabs.Tab>
					</Tabs>
					

					<Divider className='mobile-hidden'><Text size={'xl'}>Quyidagilar bilan birga qo'llash mumkin</Text></Divider>
					<Text className='desktop-hidden' weight='medium' color='red' size={'lg'}>Quyidagilar bilan birga qo'llash mumkin:</Text>

					<div className='product-list combined-product-list mt-10'>
						{combined.map((product, index) => (
							<Product product={product} key={index} />
						))}
					</div>
				</FlexboxGrid.Item>
			</FlexboxGrid>
			
      </>
   );
};

export default Page;

