import { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import { Button, Card, Text, useToaster, Message, VStack } from 'rsuite';
import store from "../store";
import { useHookstate } from '@hookstate/core';

const infoMessage = (
   <Message showIcon type='info' closable>Mahsulot buyurtma qilish uchun savatga qo'shildi.</Message>
);

const errorMessage = (
   <Message showIcon type='info' closable>Mahsulot savatdan olib tashlandi.</Message>
);

const Product = (props) => {
   const state = useHookstate(store);
   const toaster = useToaster();
   const [addedToCart, setAddedToCart] = useState(false);

   function addToCart(product) {
      const index = state.cartItems.value.findIndex(item => item.code === product.code);
      
      if (index === -1) {
         state.cartItems.set(items => [...items, product]);

         toaster.push(infoMessage, { duration: 5000})
      } else {
         state.cartItems.set(items => {
            let _items = [...items];
            _items.splice(index, 1);

            return _items;
         });

         toaster.push(errorMessage, { duration: 5000})
      }

      console.log(state.cartItems.value)
   }

   useEffect(() => {
      let added = false;

      state.cartItems.value.forEach((item) => {
         if(item.code == props.product.code) added = true;
      })

      setAddedToCart(added)
   }, [state.cartItems])

   return (
      <>
         <Card className="product-card" shaded='hover'>
            <Link to={'/product/' + props.product.code}>
               <img src={props.product.images[0].url} alt={props.product.name} className="product-image" />
            </Link>
            <Card.Header as="h6">
               <Link to={'/product/' + props.product.code}>{ props.product.name }</Link>
            </Card.Header>
            <Card.Body className="pb-0">
               <Text className='mb-3' color="red"  weight="bold">
                  { props.product.catalogPrice.value.toLocaleString('ru-RU') } so'm
               </Text>
            </Card.Body>
            <Card.Body className="mt-auto">
               <Button 
                  onClick={() => addToCart(props.product)} 
                  appearance={addedToCart ? 'ghost': 'primary'} 
                  color='orange' 
                  block
                  size="sm"
               >
                  {addedToCart ? (
                     <> <i className='bi bi-cart-check mr-2 text-xl' /> Qo'shilgan </>
                     ) : (
                     <> <i className='bi bi-cart-plus mr-2 text-xl' /> Savatga </>
                  )}
               </Button>
            </Card.Body>
         </Card>
      </>
   );
};

export default Product;
